.dialog {
  display: flex;
  flex-direction: column;

  width: 100%;
  max-width: 600px;
  max-height: calc(100% - 40px);
  background-color: #fff7ec;
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.5);
  margin: 20px;

  > .title {
    background-color: #527aa2;
    color: white;
    padding: 5px;
    font-size: 120%;
  }

  > .content {
    display: flex;
    flex-direction: column;
    padding: 20px;
    overflow-y: auto;
  }

  > .buttons {
    background-color: #f9d7a9;
    color: white;
    padding: 10px;
  }

  > .content > * {
    margin: 5px;
  }

  @media screen and (max-width: 600px), screen and (max-height: 600px) {
    margin: 0;
    max-height: 100%;
    max-width: 100%;
    height: 100%;

    > .content {
      flex-grow: 1;
    }
  }
}
